import React, { useState } from 'react';
import { Button } from '../Interface/Button';
import { TextEditor } from './TextEditor';

import { wordPressService } from '../../services/wordpress';
import styles from './Editor.module.css';

const { ipcRenderer } = window.require('electron');
const Store = window.require('electron-store');
const store = new Store();

export function Editor({ onError, onSuccess, onCredentialsReset }) {
  const [content, setContent] = useState('');
  const [isPublishing, setIsPublishing] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);

  const handleContentChange = (e) => {
    setContent(e.target.value);
  };

  const handleImageSelect = (file) => {
    setSelectedImage(file);
  };

  const handleSettingsClick = async () => {
    try {
      await ipcRenderer.invoke('open-settings');
    } catch (error) {
      console.error('Error opening settings:', error);
      onError('Failed to open settings');
    }
  };

  const handlePublish = async () => {
    try {
      if (!content.trim()) {
        onError('Please write something before publishing.');
        return;
      }

      setIsPublishing(true);

      let uploadedMedia = null;
      if (selectedImage) {
        uploadedMedia = await wordPressService.uploadMedia(selectedImage);
      }

      await wordPressService.createPost(content, uploadedMedia);
      
      setContent('');
      setSelectedImage(null);
      onSuccess('Post published successfully!');
    } catch (error) {
      console.error('Publishing error:', error);
      onError('Error publishing post: ' + error.message);
    } finally {
      setIsPublishing(false);
    }
  };

  return (
    <div className={styles.container}>
      <TextEditor 
        content={content}
        onChange={handleContentChange}
        onImageSelect={handleImageSelect}
      />

      <div className={styles.actions}>
        <Button
          variant="default"
          icon={Settings}
          onClick={handleSettingsClick}
          className={styles.settingsButton}
        >
          Settings
        </Button>
        <Button 
          variant="primary"
          onClick={handlePublish}
          disabled={isPublishing || !content.trim()}
          className={styles.publishButton}
        >
          {isPublishing ? 'Publishing...' : 'Publish'}
        </Button>
      </div>
    </div>
  );
} 